.menu-button {
    width: 30px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin: 0px;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
    span {
        display: block;
        position: absolute;
        height: 3px;
        width: 100%;
        border-radius: 9px;
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: .25s ease-in-out;
        background: #f3e5f5;
        &:nth-child(1) {
            top: 0px;
            transform-origin: left center;
        }
        &:nth-child(2) {
            top: 10px;
            transform-origin: left center;
        }
        &:nth-child(3) {
            top: 20px;
            transform-origin: left center;
        }
    }
    &.open span {
        &:nth-child(1) {
            transform: rotate(45deg);
            top: 0px;
            left: 8px;
        }
        &:nth-child(2) {
            width: 0%;
            opacity: 0;
        }
        &:nth-child(3) {
            transform: rotate(-45deg);
            top: 21px;
            left: 8px;
        }
    }
}